import React from 'react';
import { Home, Admin, Posts, Users, Comments } from '../Router';
import logo_main from '../../img/logo-black.png';
import ocean from '../../img/ocean.jpg';
import person1 from '../../img/person1.jpg';

if (typeof window !== 'undefined') {
  window.$ = $;
  window.jQuery = $;
  require('materialize-css');
}

class Index extends React.Component {
  componentDidMount() {
    //Init Side nav
    $('.button-collapse').sideNav();
    // Init Modal
    $('.modal').modal();
  }

  render() {
    return (
      <nav class="white">
        <div class="container">
          <div class="nav-wrapper">
            <a onClick={Home} class="brand-logo black-text">
              <img src={logo_main} class="logo-img" />
            </a>
            <a
              href="#"
              data-activates="side-nav"
              class="button-collapse show-on-large right black-text"
            >
              <i class="material-icons">menu</i>
            </a>
            <ul class="right hide-on-med-and-down">
              <li
                class={this.props.pages == 'index' ? 'active' : ''}
                onClick={Admin}
              >
                <a class="black-text">Dashboard</a>
              </li>
              <li
                class={this.props.pages == 'posts' ? 'active' : ''}
                onClick={Posts}
              >
                <a class="black-text">Posts</a>
              </li>
              <li
                class={this.props.pages == 'comments' ? 'active' : ''}
                onClick={Comments}
              >
                <a class="black-text">Comments</a>
              </li>
              <li
                class={this.props.pages == 'users' ? 'active' : ''}
                onClick={Users}
              >
                <a class="black-text">Users</a>
              </li>
            </ul>
            <ul id="side-nav" class="side-nav">
              <div class="user-view">
                <div class="background">
                  <img src={ocean} alt="" />
                </div>
                <a style={{ marginTop: '30px' }}>
                  <img src={person1} alt="" class="circle" />
                </a>
                <a>
                  {' '}
                  <span class="name white-text">John Doe</span>{' '}
                </a>
                <a style={{ marginBottom: '5px' }}>
                  <span class="email white-text">jdoe@gmail.com</span>
                </a>
              </div>
              <li>
                <a onClick={Admin}>
                  <i class="material-icons">dashboard</i> Dashboard
                </a>
              </li>
              <li>
                <a onClick={Posts}>Posts</a>
              </li>
              <li>
                <a onClick={Comments}>Comments</a>
              </li>
              <li>
                <a onClick={Users}>Users</a>
              </li>
              <li>
                <div class="divider" />
              </li>
              <li>
                <a class="subheader">Account Controls</a>
              </li>
              <li>
                <a class="waves-effect">Logout</a>
              </li>
            </ul>
          </div>
        </div>

        {/*<!-- Fixed Action Button --> */}
        <div class="fixed-action-btn">
          <a class="btn-floating btn-large red">
            <i class="material-icons">add</i>
          </a>
          <ul>
            <li>
              <a
                href="#post-modal"
                class="modal-trigger btn-floating grey darken-4"
              >
                <i class="material-icons">mode_edit</i>
              </a>
            </li>
            <li>
              <a
                href="#category-modal"
                class="modal-trigger btn-floating  grey darken-4"
              >
                <i class="material-icons">folder</i>
              </a>
            </li>
            <li>
              <a
                href="#user-modal"
                class="modal-trigger btn-floating  grey darken-4"
              >
                <i class="material-icons">supervisor_account</i>
              </a>
            </li>
          </ul>
        </div>

        {/* <!-- Add User Modal -->*/}
        <div id="category-modal" class="modal">
          <div class="modal-content black-text">
            <h4>Add Category</h4>
            <form>
              <div class="input-field">
                <input type="text" id="title" />{' '}
                <label htmlFor="title">Title</label>
              </div>
            </form>
            <div class="modal-footer">
              <a href="#!" class="modal-action modal-close btn blue white-text">
                Submit
              </a>
            </div>
          </div>
        </div>

        {/* <!-- Add User Modal -->*/}
        <div id="user-modal" class="modal">
          <div class="modal-content black-text">
            <h4>Add User</h4>
            <form>
              <div class="input-field">
                <input type="text" id="name" />{' '}
                <label htmlFor="name">Name</label>
              </div>
              <div class="input-field">
                <input type="email" id="email" />{' '}
                <label htmlFor="email">Email</label>
              </div>
              <div class="input-field">
                <input type="password" id="password" />
                <label htmlFor="password">Password</label>
              </div>
              <div class="input-field">
                <input type="password" id="password2" />
                <label htmlFor="password2">Confirm Password</label>
              </div>
            </form>
            <div class="modal-footer">
              <a href="#!" class="modal-action modal-close btn blue white-text">
                Submit
              </a>
            </div>
          </div>
        </div>
      </nav>
    );
  }
}

export default Index;
